import React from 'react'
import { Link } from "react-router-dom"
import logo from "./../../../../../assets/img/031-paw.png"

const PatitasHomeHeader = () => {
    return (
        <header>
            <nav className="navbar navbar-expand-sm navbar-light p-0">

                <div className="bg-primary d-sm-none py-2 px-3 header__div">
                    <Link className="navbar-brand text-center text-white font-weight-bold mr-0 header__brand" to="/patitas/home">
                        P<small className="font-weight-bold">ATITAS</small> 
                        P<small className="font-weight-bold">ERDIDAS</small>
                        <br/>
                        <div>
                            <img className="header__logo" src={logo} alt="logo"/>
                        </div>
                    </Link>
                </div>
                
                <button className="navbar-toggler mr-3 collapse__icon" type="button" data-toggle="collapse" data-target="#navbarSupportedContent" >
                    <span className="navbar-toggler-icon"></span>
                </button>

                <div className="collapse navbar-collapse justify-content-center" id="navbarSupportedContent">
                    <Link className="nav-link font-weight-bold" to="/patitas/buscar">BUSCAR</Link>
                </div>

                <div className="bg-primary d-none d-sm-block py-2 px-3 header__div">
                    <Link className="navbar-brand text-center text-white font-weight-bold mr-0 header__brand" to="/patitas/home">
                        P<small className="font-weight-bold">ATITAS</small> 
                        P<small className="font-weight-bold">ERDIDAS</small>
                        <br/>
                        <div>
                            <img className="header__logo" src={logo} alt="logo"/>
                        </div>
                    </Link>
                </div>
                
                <div className="collapse navbar-collapse justify-content-center" id="navbarSupportedContent">
                    <Link className="nav-link font-weight-bold" to="/patitas/registrar">REGISTRAR</Link>
                </div>

            </nav>
        </header>
    )
}

export default PatitasHomeHeader
