import React from 'react'
import { Switch, Route } from "react-router-dom"
import PatitasHomePage from './pages/home/PatitasHomePage'
import PatitasRegistrarPage from './pages/registrar/PatitasRegistrarPage'
import PatitasBuscarPage from './pages/buscar/PatitasBuscarPage'

const PatitasRouter = () => {
    return (
        
        <Switch>
            <Route path="/patitas/home" component={PatitasHomePage} />
            <Route path="/patitas/registrar" component={PatitasRegistrarPage} />
            <Route path="/patitas/buscar" component={PatitasBuscarPage} />
        </Switch>
        
    )
}

export default PatitasRouter
