import React, { useContext, useState, useEffect } from 'react'
import AuthContext from '../../../../context/auth/authContext';
import { getUsuarios } from '../../../../services/authService';
import { Formik } from 'formik'
import * as Yup from 'yup'
import Swal from "sweetalert2";

const AuthLoginPage = (props) => {

    const { iniciarSesionContext } = useContext(AuthContext);

    const [usuarios, setUsuarios] = useState([])

    useEffect(() => {
        getUsuarios().then(data => {
            setUsuarios(data)
        })
    }, [])

    return (

            <div className="container my-5">

                <div className="row justify-content-center mb-3">
                    <h3 className="text-center">Admin</h3>
                </div>

                <div className="row justify-content-center">

                    <Formik
                        initialValues = {{
                            correo: "",
                            contrasena: ""
                        }}

                        validationSchema = { Yup.object().shape({
          
                            correo: Yup.string().required("requerido"),                
                            contrasena: Yup.string().required("requerido"),                

                        })}

                        onSubmit = { (values, actions) => {

                            const usuarioFiltrado = usuarios.filter( usu => usu.correo === values.correo );
                            if ( usuarioFiltrado.length > 0 && usuarioFiltrado[0].contrasena === values.contrasena ) {
                                iniciarSesionContext(
                                    // usuarioFiltrado
                                    {
                                        id : usuarioFiltrado[0].id,  
                                        nombre : usuarioFiltrado[0].nombre,  
                                        apellido : usuarioFiltrado[0].apellido,  
                                    }
                                );
                                actions.setSubmitting(false);
                                actions.resetForm();
                                Swal.fire({
                                    width: "400px",
                                    title: "¡Hecho!",
                                    text: 'Sesión iniciada',
                                    icon: "success",
                                    iconColor: "#56cc9d",
                                    timer: 1000,
                                    position: "top-right",
                                    showConfirmButton: false
                                });
                                props.history.push("/admin/mascotas")
                            } else {
                                Swal.fire({
                                    width: "400px",
                                    title: "Error",
                                    text: 'Correo o contraseña incorrectos',
                                    icon: "error",
                                    iconColor: "#FF7851",
                                    timer: 1000,
                                    position: "top-right",
                                    showConfirmButton: false
                                });
                            }
                        }}
                    >
                        {props => (
                            <form className="bg-light p-5 form__div" onSubmit={props.handleSubmit}>

                                <div className="form-row">
                                    <div className="form-group col">
                                        <label>Correo</label>
                                        <input
                                            className="form-control"
                                            placeholder="correo"
                                            type="email"
                                            name="correo"
                                            value={props.values.correo}
                                            onChange={props.handleChange}
                                        />
                                        {props.errors.correo && props.touched.correo && (
                                            <div className="text-danger ml-3"><small>{props.errors.correo}</small></div>
                                        )}
                                    </div>
                                </div>

                                <div className="form-row">
                                    <div className="form-group col">
                                        <label>Contraseña</label>
                                        <input
                                            className="form-control"
                                            placeholder="contraseña"
                                            type="password"
                                            name="contrasena"
                                            value={props.values.contrasena}
                                            onChange={props.handleChange}
                                        />
                                        {props.errors.contrasena && props.touched.contrasena && (
                                            <div className="text-danger ml-3"><small>{props.errors.contrasena}</small></div>
                                        )}
                                    </div>
                                </div>    

                                <div className="d-flex justify-content-center mt-2">
                                    <button type="submit" className="btn btn-primary">Iniciar Sesión</button>
                                </div>

                            </form>
                        )}

                    </Formik>

                </div>

            </div>

    )
}

export default AuthLoginPage