import { url_backend } from "../environments/environments";

export const getProvincias = async () => {
    const peticion = await fetch(`${url_backend}/provincia`)
    const data = await peticion.json();
    return data;
}

export const getDistritosByProvinciaId = async (id) => {
    const peticion = await fetch(`${url_backend}/provincia/${id}/distrito`)
    const data = await peticion.json();
    return data;
}

export const getDistritos = async () => {
    const peticion = await fetch(`${url_backend}/distrito`)
    const data = await peticion.json();
    return data;
}