import React, { useState, useEffect } from 'react'
import Select from 'react-select';
import makeAnimated from 'react-select/animated';
import { getMascotas } from '../../../../../services/mascotasService'
import { getDistritos, getProvincias } from '../../../../../services/provinciasService';

const PatitasBuscarMain = () => {

    const [provincias, setProvincias] = useState([]);
    const [distritos, setDistritos] = useState([]);
    const [mascotas, setMascotas] = useState([]);
    const [mascotasFiltradas, setMascotasFiltradas] = useState([]);

    const obtenerProvincias = () => {
        getProvincias().then(data => {
            setProvincias(data);
        })
    }
    const obtenerDistritos = () => {
        getDistritos().then(data => {
            setDistritos(data);
        })
    }
    const obtenerMascotas = () => {
        getMascotas().then(data => {
            setMascotas(data);
        })
    }

    useEffect(() => {
        obtenerProvincias();
        obtenerDistritos();
        obtenerMascotas();
    }, [])

    const animados = makeAnimated();

    const estadosOpciones = [
        { value: 'Perdido', label: 'Perdidos' },
        { value: 'Encontrado', label: 'Encontrados' },
    ];

    const tiposOpciones = [
        { value: 'Perro', label: 'Perros' },
        { value: 'Gato', label: 'Gatos' },
        { value: 'Conejo', label: 'Conejos' },
        { value: 'Roedor', label: 'Roedores' },
        { value: 'Ave', label: 'Aves' },
        { value: 'Reptil', label: 'Reptiles' },
        { value: 'Otro', label: 'Otros' },
    ];

    const mesesOpciones = [
        { value: '01', label: 'Enero' },
        { value: '02', label: 'Febrero' },
        { value: '03', label: 'Marzo' },
        { value: '04', label: 'Abril' },
        { value: '05', label: 'Mayo' },
        { value: '06', label: 'Junio' },
        { value: '07', label: 'Julio' },
        { value: '08', label: 'Agosto' },
        { value: '09', label: 'Setiembre' },
        { value: '10', label: 'Octubre' },
        { value: '11', label: 'Noviembre' },
        { value: '12', label: 'Diciembre' },
    ];

    let aniosOpciones = [];
    for (let i=new Date().getFullYear(); i >= 2015 ;i--) {
        aniosOpciones.push({ value: i.toString(), label: i});
    }

    let provinciasOpciones = [];
    provincias.forEach(objProvincia => {
        provinciasOpciones.push({ value: objProvincia.id, label: objProvincia.nombre})
    })

    let distritosOpciones = [];
    distritos.forEach(objDistrito => {
        distritosOpciones.push({ value: objDistrito.id, label: objDistrito.nombre})
    })
    
    const filtrar = () => {
        let mascotasTemp = [];
        mascotas.forEach( mas => {
            let accepted = true;

            if (estadosValores.length > 0) {
                let acceptedEstados = false;
                estadosValores.forEach(val => {
                    if (val === mas.estado) {
                        acceptedEstados = acceptedEstados || true;
                    } else {
                        acceptedEstados = acceptedEstados || false;
                    }
                })
                acceptedEstados ? accepted = accepted && true : accepted = accepted && false;
            }
            
            if (tiposValores.length > 0) {
                let acceptedTipos = false;
                tiposValores.forEach(val => {
                    if (val === mas.especie) {
                        acceptedTipos = acceptedTipos || true;
                    } else {
                        acceptedTipos = acceptedTipos || false;
                    }
                })
                acceptedTipos ? accepted = accepted && true : accepted = accepted && false;
            }
            
            if (provinciasValores.length > 0) {
                let acceptedProvincias = false;
                provinciasValores.forEach(val => {
                    if (val === mas.provincia) {
                        acceptedProvincias = acceptedProvincias || true;
                    } else {
                        acceptedProvincias = acceptedProvincias || false;
                    }
                })
                acceptedProvincias ? accepted = accepted && true : accepted = accepted && false;
            }
            
            if (distritosValores.length > 0) {
                let acceptedDistritos = false;
                distritosValores.forEach(val => {
                    if (val === mas.distrito) {
                        acceptedDistritos = acceptedDistritos || true;
                    } else {
                        acceptedDistritos = acceptedDistritos || false;
                    }
                })
                acceptedDistritos ? accepted = accepted && true : accepted = accepted && false;
            }
        
            if (aniosValores.length > 0) {
                let acceptedAnios = false;
                aniosValores.forEach(val => {
                    if( val === mas.fecha.split("-")[0] ){
                        acceptedAnios = acceptedAnios || true;
                    } else {
                        acceptedAnios = acceptedAnios || false;
                    }
                });
                acceptedAnios ? accepted = accepted && true : accepted = accepted && false;
            }
    
            if (mesesValores.length > 0) {
                let acceptedMeses = false;
                mesesValores.forEach(val => {
                    if( val === mas.fecha.split("-")[1] ){
                        acceptedMeses = acceptedMeses || true;
                    } else {
                        acceptedMeses = acceptedMeses || false;
                    }
                });
                acceptedMeses ? accepted = accepted && true : accepted = accepted && false;
            }
    
            if (accepted) {
                mascotasTemp.push(mas);
            }
        });
        setMascotasFiltradas(mascotasTemp)
    }

    const [estadosValores, setEstadosValores] = useState([])
    const handleChangeEstados = val => {
        let tempEstadosValores = [];
        val.forEach(v => tempEstadosValores.push(v.value));
        setEstadosValores(tempEstadosValores)
    }

    const [tiposValores, setTiposValores] = useState([])
    const handleChangeTipos = val => {
        let tempTiposValores = [];
        val.forEach(v => tempTiposValores.push(v.value));
        setTiposValores(tempTiposValores)
    }

    const [provinciasValores, setProvinciasValores] = useState([]);
    const handleChangeProvincias = val => {
        let tempProvinciasValores = [];
        val.forEach(v => tempProvinciasValores.push(v.label));
        setProvinciasValores(tempProvinciasValores)
    }

    const [distritosValores, setDistritosValores] = useState([])
    const handleChangeDistritos = val => {
        let tempDistritosValores = [];
        val.forEach(v => tempDistritosValores.push(v.label));
        setDistritosValores(tempDistritosValores)
    }

    const [aniosValores, setAniosValores] = useState([])
    const handleChangeAnios = val => {
        let tempAniosValores = [];
        val.forEach(v => tempAniosValores.push(v.value));
        setAniosValores(tempAniosValores)
    }

    const [mesesValores, setMesesValores] = useState([])
    const handleChangeMeses = val => {
        let tempMesesValores = [];
        val.forEach(v => tempMesesValores.push(v.value));
        setMesesValores(tempMesesValores)
    }

    useEffect(() => {
        console.log(mascotas)
        filtrar();
    }, [mascotas, estadosValores, tiposValores, provinciasValores, distritosValores, aniosValores, mesesValores])

    return (
        <main>
            <div className="container-fluid">
                <div className="row justify-content-center my-5">

                    <div className="col-sm-5 col-md-4 col-lg-3">

                        <div className="row flex-column">

                            <div className="col justify-content-center mb-2">
                                <h5 className="text-center">Filtros</h5>
                            </div>

                            <div className="col justify-content-center">
                                <form className="bg-light pt-3 px-3 buscar__div">
                                    <fieldset>

                                        <div className="form-group">
                                            <Select
                                                closeMenuOnSelect={true}
                                                components={animados}
                                                isMulti
                                                options={estadosOpciones}
                                                placeholder={'Estados'}
                                                onChange={handleChangeEstados}
                                            />
                                        </div>
                                
                                        <div className="form-group">
                                            <Select
                                                closeMenuOnSelect={true}
                                                components={animados}
                                                isMulti
                                                options={tiposOpciones}
                                                placeholder={'Tipos'}
                                                onChange={handleChangeTipos}
                                            />
                                        </div>
                                                                                    
                                        <div className="form-group">
                                            <input className="form-control" type="text" placeholder="Lima" disabled/>
                                        </div>

                                        <div className="form-group">
                                            <Select
                                                closeMenuOnSelect={true}
                                                components={animados}
                                                isMulti
                                                options={provinciasOpciones}
                                                placeholder={'Provincias'}
                                                onChange={handleChangeProvincias}
                                            />
                                        </div>

                                        <div className="form-group">
                                            <Select
                                                closeMenuOnSelect={true}
                                                components={animados}
                                                isMulti
                                                options={distritosOpciones}
                                                placeholder={'Distritos'}
                                                onChange={handleChangeDistritos}
                                            />
                                        </div>

                                        <div className="form-group">
                                            <Select
                                                closeMenuOnSelect={true}
                                                components={animados}
                                                isMulti
                                                options={aniosOpciones}
                                                placeholder={'Años'}
                                                onChange={handleChangeAnios}
                                            />
                                        </div>

                                        <div className="form-group">
                                            <Select
                                                closeMenuOnSelect={true}
                                                components={animados}
                                                isMulti
                                                options={mesesOpciones}
                                                placeholder={'Meses'}
                                                onChange={handleChangeMeses}
                                            />
                                        </div>
                                        
                                    </fieldset>
                                </form>
                            </div>

                        </div>

                    </div>

                    <div className="col-sm-7 col-md-8 col-lg-9">
                        <div className="row" id="cardsMascotas">
                            {
                                mascotasFiltradas.length > 0 ?
                                    (
                                        mascotasFiltradas.map(mas => {
                                            let dist = mas.distrito !== "" ? ` - ${mas.distrito}` : "";
                                            let tel = mas.telefono !== "" ? <>{mas.telefono}<br/></> : "";
                                            let email = mas.email !== "" ? mas.email : "";
                                            let cont = (tel !== "" || email !== "") ? <li className="list-group-item"><small>{tel} {email}</small></li> : "";
                                            return (
                                                <div className="col-sm-12 col-md-6 col-lg-4 col-xl-3 my-4 d-flex justify-content-center"> 
                                                    <div className="card shadow h-100 w-100 border-primary cardmascota">

                                                        <div className="card-header d-flex justify-content-between cardmascota__header">
                                                            <div><small><b>{(mas.especie).toUpperCase()}</b></small></div>
                                                            <span className="badge badge-primary disabled pt-1">{mas.estado}</span>
                                                        </div>

                                                        <img src={mas.imagen} className="d-block cardmascota__img" alt=""/>

                                                        <div className="card-body d-flex align-items-center justify-content-center">
                                                            <p className="card-text text-center"><small><b>{mas.descripcion}</b></small></p>
                                                        </div>

                                                        <ul className="list-group list-group-flush">
                                                            <li className="list-group-item"><small>{mas.departamento} - {mas.provincia} {dist}</small></li>
                                                            <li className="list-group-item"><small>{mas.direccion}</small></li>
                                                            {cont}
                                                        </ul>

                                                        <div className="card-footer text-center cardmascota__footer">
                                                            <small>{mas.fecha}</small>
                                                        </div>

                                                    </div>
                                                </div>
                                            )
                                        })
                                    )
                                :
                                ""
                            }
                        </div>
                    </div>

                </div>
            </div>
        </main>
    )
}

export default PatitasBuscarMain
