import React, { useEffect, useState } from 'react'
import { getProvincias } from '../../../../../services/provinciasService'

const PatitasRegistrarProvincia = () => {

    const [provincias, setProvincias] = useState([]);

    useEffect(() => {
        getProvincias().then(data => {
            if (data) {
                setProvincias(data);
            }
        })
    }, []);

    return (
        provincias.map(objProvincia => {
            return (
                <option key={objProvincia.id} value={objProvincia.nombre}>{objProvincia.nombre}</option>
            );
        })
    )
}

export default PatitasRegistrarProvincia
