import React, { useEffect, useState } from 'react'
import { useHistory } from "react-router-dom";
import AuthContext from './authContext';

const AuthState = ({ children }) => {

    const history = useHistory();

    const [auth, setAuth] = useState({
        autenticado: false,
        usu_nom: null,
        usu_ape: null,
        usu_id: null,
        cargando: true
    });

    const cerrarSesion = () => {
        localStorage.removeItem("usuario");
        history.replace("/auth");
        setAuth({
            autenticado: false,
            usu_nom: null,
            usu_ape: null,
            usu_id: null,
            cargando: true
        })
    }
    
    const iniciarSesionContext = usu => {
        localStorage.setItem("usuario", JSON.stringify(usu));
        setAuth({
            autenticado: true,
            usu_nom: usu.nombre,
            usu_ape: usu.apellido,
            usu_id: usu.id,
            cargando: false
        });
    }

    const iniciarSesionConLocalStorage = () => {
        const usu = localStorage.getItem("usuario");
        if (usu) {
            setAuth({
                autenticado: true,
                usu_nom: usu.nombre,
                usu_ape: usu.apellido,
                usu_id: usu.id,
                cargando: false
            });
        } 
        else {
        // No hacer nada o redireccionar al home
            setAuth({
                autenticado: false,
                usu_nom: null,
                usu_ape: null,
                usu_id: null,
                cargando: false
            })
        }
    }

    useEffect(() => {
        iniciarSesionConLocalStorage();
    }, [])

    return (
        <AuthContext.Provider 
            value={{
                autenticado: auth.autenticado,
                usu_nom: auth.usu_nom,
                usu_ape: auth.usu_ape,
                usu_id: auth.usu_id,
                cargando: auth.cargando,
                iniciarSesionContext: iniciarSesionContext,
                cerrarSesion: cerrarSesion
            }}
        >
            {children}
        </AuthContext.Provider>
    )
}

export default AuthState
