import React from 'react'
import { Link } from "react-router-dom"
import perdida from "./../../../../../assets/img/001-dog-house.png"
import encontrada from "./../../../../../assets/img/039-cat-box.png"

const PatitasHomeMain = () => {
    return (
        <main>
            <div className="container py-5 px-4 my-5">
                <div className="row">
                    <div className="col d-flex flex-column align-items-center justify-content-center bg-light py-5 mx-4 mb-5 home__div">
                        <h4 className="text-center mx-4">¿Encontraste una mascota?</h4>
                        <div><img className="home__img" src={encontrada} alt=""/></div>
                        <div className="d-flex flex-column align-items-center">
                            <Link to="/patitas/buscar" className="btn btn-warning mt-3">Ver si alguien la busca</Link>
                            <Link to="/patitas/registrar" className="btn btn-info mt-3">¡Regístrala!</Link>
                        </div>
                    </div>
                    <div className="col d-flex flex-column align-items-center justify-content-center bg-light py-5 mx-4 mb-5 home__div">
                        <h4 className="text-center mx-4">¿Perdiste a tu mascota?</h4>
                        <div><img className="home__img" src={perdida} alt=""/></div>
                        <div className="d-flex flex-column align-items-center">
                            <Link to="/patitas/buscar" className="btn btn-secondary mt-3">Buscar en las encontradas</Link>
                            <Link to="/patitas/registrar" className="btn btn-info mt-3">¡Regístrala!</Link>
                        </div>
                    </div>
                </div>
            </div>
        </main>
    )
}

export default PatitasHomeMain
