import React from 'react'
import PatitasRegistrarForm from './PatitasRegistrarForm'
import gato from "./../../../../../assets/img/046-cat.png"
import conejo from "./../../../../../assets/img/014-rabbit.png"
import tortuga from "./../../../../../assets/img/040-turtle.png"
import perro from "./../../../../../assets/img/030-dog.png"
import pajaro from "./../../../../../assets/img/047-lovebird.png"
import hamster from "./../../../../../assets/img/045-hamster.png"

const PatitasRegistrarMain = () => {
    return (
        <main>
            <div className="row">

                <div className="col-2 col-lg-3 d-none d-md-flex flex-column align-items-end justify-content-center">
                    <img className="my-5 registrar__img" src={gato} alt=""/>
                    <img className="my-5 registrar__img" src={conejo} alt=""/>
                    <img className="my-5 registrar__img" src={tortuga} alt=""/>
                </div>
                
                <div className="col-12 col-md-8 col-lg-6">

                    <div className="container my-5">

                        <div className="row justify-content-center mb-3">
                            <h3 className="text-center">Registra a una mascota</h3>
                        </div>

                        <div className="row justify-content-center">
                            <PatitasRegistrarForm />
                        </div>

                    </div>

                </div>
        
                <div className="col-2 col-lg-3 d-none d-md-flex flex-column align-items-start justify-content-center">
                    <img className="my-5 registrar__img" src={perro} alt=""/>
                    <img className="my-5 registrar__img" src={pajaro} alt=""/>
                    <img className="my-5 registrar__img" src={hamster} alt=""/>
                </div>
                
            </div>
        </main>
    )
}

export default PatitasRegistrarMain
