import React, { useContext } from 'react'
import { Link } from "react-router-dom"
import AuthContext from '../../../context/auth/authContext'
import logo from "./../../../assets/img/031-paw.png"

const AdminHeader = () => {

    const { usu_nom, usu_ape, cerrarSesion } = useContext(AuthContext)

    return (
        <header>
            <nav className="navbar navbar-dark bg-primary p-0">

                <div className="py-1 px-3">
                    <Link className="navbar-brand text-white font-weight-bold header__brand" to="/admin">
                        <img className="admin__header__logo mr-2" src={logo} alt="logo"/>
                        P<small className="font-weight-bold mr-1">ATITAS</small> 
                        P<small className="font-weight-bold">ERDIDAS</small>
                    </Link>
                </div>

                <div className="px-3">
                    <span className="navbar-text mr-2">
                        ¡Hola, {usu_nom} {usu_ape} !
                    </span>
    
                    <button className="btn btn-danger btn-sm" onClick={() => { cerrarSesion() }}>
                        <i className="fas fa-sign-out-alt"></i>
                    </button>
                </div>

            </nav>
        </header>
    )
}

export default AdminHeader
 