import React, { useEffect, useState } from 'react'
import { getDistritos } from '../../../../../services/provinciasService'

const PatitasRegistrarDistrito = (props) => {

    const [distritos, setDistritos] = useState([]) 

    useEffect(() => {
        if (props.provincia === "Lima") {
            getDistritos().then(data => {
            if (data) {
                setDistritos(data);
            }
          })
        }
        else {
            setDistritos([]);
        }
      }, [props.provincia]);

    return (
        props.provincia ?
        (
            distritos.map(objDistrito => {
                return (
                    <option key={objDistrito.id} value={objDistrito.nombre}>{objDistrito.nombre}</option>
                )
            })
            )
            : 
            ""
    )
}

export default PatitasRegistrarDistrito
