import React from 'react'
import { Switch, Route } from "react-router-dom"
import AdminHeader from './components/AdminHeader'
import AdminMascotasPage from './pages/mascotas/AdminMascotasPage'

const AdminRouter = () => {
    return (
        <>
        <AdminHeader />
            <Switch>
                <Route path="/admin" component={AdminMascotasPage} />
            </Switch>
        </>
    )
}

export default AdminRouter
