import { url_backend } from "../environments/environments";

export const getMascotas = async () => {
    let peticion = await fetch(`${url_backend}/mascota`)
    let data = await peticion.json();
    return data;
}

export const deleteMascotaById = async (id) => {
  let peticion = await fetch(`${url_backend}/mascota/${id}`, {
    method: "DELETE"
  });
  let data = await peticion.json();
  return data;
}

export const postMascota = async (objMascota) => {
    let peticion = await fetch(`${url_backend}/mascota`, {
      method: "POST",
      body: JSON.stringify(objMascota),
      headers: {
        "Content-type": "application/json"
      }
    });
    let data = await peticion.json();
    return data;
}

export const updateMascota = async (id, objMascota) => {
    let peticion = await fetch(`${url_backend}/mascota/${id}`, {
      method: "PUT",
      body: JSON.stringify(objMascota),
      headers: {
        "Content-type": "application/json"
      }
    });
    let data = await peticion.json();
    return data;
}