import React, { useEffect, useState } from 'react'
import { MDBDataTableV5 } from 'mdbreact'
import { deleteMascotaById, getMascotas, updateMascota, postMascota } from '../../../../services/mascotasService'
import { Formik, Field } from 'formik'
import * as Yup from 'yup'
import Swal from "sweetalert2";
import PatitasRegistrarProvincia from '../../../patitas/pages/registrar/components/PatitasRegistrarProvincia'
import PatitasRegistrarDistrito from '../../../patitas/pages/registrar/components/PatitasRegistrarDistrito'

const AdminMascotasPage = () => {

    const [mascotaActual, setMascotaActual] = useState({})

    const [refresh, setRefresh] = useState(0)

    const [datatable, setDatatable] = useState({
        columns : [
            { label: "ID", field: "id" },
            { label: "ESPECIE", field: "especie" },
            { label: "ESTADO", field: "estado" },
            { label: "DESCRIPCIÓN", field: "descripcion", sort: 'disabled' },
            { label: "DEPARTAMENTO", field: "departamento", sort: 'disabled' },
            { label: "PROVINCIA", field: "provincia" },
            { label: "DISTRITO", field: "distrito" },
            { label: "DIRECCIÓN", field: "direccion", sort: 'disabled' },
            { label: "FECHA", field: "fecha" },
            { label: "TELÉFONO", field: "telefono", sort: 'disabled' },
            { label: "EMAIL", field: "email", sort: 'disabled' },
            { label: "IMAGEN", field: "imagen", sort: 'disabled' },
            { label: "ACCIONES", field: "acciones", sort: 'disabled' }
        ],
        rows : []
    })

    useEffect(() => {
        getMascotas().then(data => {
            const filas = data.map( mas => {
                return {
                    id: +mas.id,
                    especie: mas.especie,
                    estado: mas.estado,
                    descripcion: mas.descripcion,
                    departamento: mas.departamento,
                    provincia: mas.provincia,
                    distrito: mas.distrito,
                    direccion: mas.direccion,
                    fecha: mas.fecha,
                    telefono: mas.telefono,
                    email: mas.email,
                    imagen: <img src={mas.imagen} className="d-block tablemascota__img" alt=""/>,
                    acciones: 
                    <>
                    <button type="button" className="btn btn-info btn-sm mr-2" data-toggle="modal" data-target="#modalEditar"
                    onClick={ () => {
                        setMascotaActual({
                            id: mas.id,
                            estado: mas.estado,             /* radio             */
                            especie: mas.especie,               /* select            */
                            descripcion: mas.descripcion,        /* input text        */
                            imagen: "",               /* input file        */
                            departamento: mas.departamento,   /* string            */
                            provincia: mas.provincia,          /* select            */
                            distrito: mas.distrito,           /* select            */
                            direccion: mas.direccion,          /* input text        */
                            fecha: mas.fecha,              /* input date        */
                            telefono: mas.telefono,           /* input tel         */
                            email: mas.email               /* input email       */
                        })
                    }}
                    ><i className="far fa-edit"></i></button>
                    <button type="button" className="btn btn-danger btn-sm" 
                    onClick={ () => {
                        Swal.fire({
                            width: "400px",
                            title: "¿Eliminar registro?",
                            text: "No se podrá deshacer",
                            icon: "warning",
                            iconColor: "#FFCE67",
                            showCancelButton: true,
                            confirmButtonColor: '#56cc9d',
                            cancelButtonColor: '#FF7851',
                            confirmButtonText: '¡Sí, elimínalo!',
                            cancelButtonText: 'Cancelar',
                        }).then((result) => {
                            if (result.isConfirmed) {
                                deleteMascotaById(mas.id).then(()=>{ 
                                    setRefresh(refresh+1) 
                                })
                                Swal.fire({
                                    width: "400px",
                                    title: "¡Hecho!",
                                    text: "El registro ha sido eliminado.",
                                    icon: "success",
                                    iconColor: "#56cc9d",
                                    timer: 1000,
                                    position: "top-right",
                                    showConfirmButton: false
                                })
                            }
                          });
                    }}
                    ><i className="far fa-trash-alt"></i></button>
                    </>
                }
            })
            setDatatable({
                ...datatable,
                rows: filas
            })
        })
    }, [refresh])

    let fecha = new Date()
    let fechaHoy = new Date(fecha.getTime() - fecha.getTimezoneOffset() * 60000).toISOString().split("T")[0];

    return (
        <main>
            <div className="modal fade" id="modalEditar" tabIndex="-1">

                <div className="modal-dialog">

                    <div className="modal-content">

                        <div className="modal-header">
                            <h5 className="modal-title">Editar Registro</h5>
                            <button type="button" className="close collapse__icon" data-dismiss="modal">
                            <span>&times;</span>
                            </button>
                        </div>

                        <div className="modal-body">

                            <Formik
                                enableReinitialize={true}

                                initialValues = {
                                    mascotaActual        
                                }

                                validationSchema = { Yup.object().shape({

                                    estado: Yup.string().required("requerido"),             
                                    especie: Yup.string().required("requerido"),               
                                    descripcion: Yup.string().trim().required("requerido"),        
                                    imagen: Yup.string(),               
                                    departamento: Yup.string(),  
                                    provincia: Yup.string().required("requerido"),         
                                    distrito: Yup.string(),          
                                    direccion: Yup.string().trim().required("requerido"),          
                                    fecha: Yup.string().required("requerido"),              
                                    telefono: Yup.string().matches(/([0-9]{7}|[0-9]{9})/, 'no válido', {excludeEmptyString: true} ),          
                                    email: Yup.string()               

                                })}

                                onSubmit = { (values, actions) => {
                                    updateMascota(mascotaActual.id, values).then((data) => {
                                        actions.setSubmitting(false);
                                        actions.resetForm();
                                        setRefresh(refresh+1);
                                    })
                                    Swal.fire({
                                        width: "400px",
                                        title: "¡Hecho!",
                                        text: "Cambios guardados",
                                        icon: "success",
                                        iconColor: "#56cc9d",
                                        timer: 1000,
                                        position: "top-right",
                                        showConfirmButton: false
                                    });
                                }}

                            >
                                { props => (

                                    <form className="px-3 py-2" onSubmit={props.handleSubmit}>

                                        <div className="form-group">
                                            <label>Estado</label>
                                                <div className="d-md-flex">
                                                    <label className="mr-4">
                                                        <Field 
                                                            className="mr-2"
                                                            type="radio" 
                                                            name="estado" 
                                                            value="Perdido"
                                                            onChange={props.handleChange}
                                                        />
                                                        Mascota Perdida
                                                    </label>
                                                    <label>
                                                        <Field 
                                                            className="mr-2"
                                                            type="radio" 
                                                            name="estado" 
                                                            value="Encontrado"
                                                            onChange={props.handleChange}
                                                        />
                                                        Mascota Encontrada
                                                    </label>
                                                </div>
                                                {props.errors.estado && props.touched.estado && (
                                                    <div className="text-danger ml-3"><small>{props.errors.estado}</small></div>
                                                )}
                                        </div>

                                        <div className="form-row">
                                            <div className="form-group col-md-4">
                                                <label>Tipo</label>
                                                <Field
                                                    className="form-control"
                                                    as="select"
                                                    name="especie"
                                                    onChange={props.handleChange}
                                                >
                                                    <option selected disabled value="">Tipo</option>
                                                    <option value="Perro">Perro</option>
                                                    <option value="Gato">Gato</option>
                                                    <option value="Conejo">Conejo</option>
                                                    <option value="Roedor">Roedor</option>
                                                    <option value="Ave">Ave</option>
                                                    <option value="Reptil">Reptil</option>
                                                    <option value="Otro">Otro</option>
                                                </Field>
                                                {props.errors.especie && props.touched.especie && (
                                                    <div className="text-danger ml-3"><small>{props.errors.especie}</small></div>
                                                )}
                                            </div> 
                    
                                            <div className="form-group col-md-8">
                                                <label>Descripción</label>
                                                <Field
                                                    className="form-control"
                                                    placeholder="Añade una descripción"
                                                    rows="3"
                                                    as="textarea"
                                                    name="descripcion"
                                                    value={props.values.descripcion}
                                                    onChange={props.handleChange}
                                                />
                                                {props.errors.descripcion && props.touched.descripcion && (
                                                    <div className="text-danger ml-3"><small>{props.errors.descripcion}</small></div>
                                                )}
                                            </div>
                                        </div>

                                        <div className="form-group">
                                            <div className="input-group mb-3">
                                                <div className="custom-file">
                                                    <label className="custom-file-label">Sube una foto</label>
                                                    <input
                                                        className="custom-file-input"
                                                        type="file"
                                                        name="imagen"
                                                        value={props.values.imagen}
                                                        onChange={props.handleChange}
                                                    />
                                                    {props.errors.imagen && props.touched.imagen && (
                                                        <div className="text-danger ml-3"><small>{props.errors.imagen}</small></div>
                                                    )}
                                                </div>
                                            </div>
                                        </div>

                                        <div className="form-row">
                                            <div className="form-group col-md-4">
                                                <label>Departamento</label>
                                                <input
                                                    className="form-control"
                                                    placeholder="Lima"
                                                    disabled
                                                    type="text"
                                                    name="departamento"
                                                    value={props.values.departamento}
                                                    onChange={props.handleChange}
                                                />
                                                {props.errors.departamento && props.touched.departamento && (
                                                    <div className="text-danger ml-3"><small>{props.errors.departamento}</small></div>
                                                )}
                                            </div>

                                            <div className="form-group col-md-4">
                                                <label>Provincia</label>
                                                <Field
                                                    className="form-control"
                                                    as="select"
                                                    name="provincia"
                                                    onChange={props.handleChange}
                                                >
                                                    <option selected disabled value="">Provincia</option>
                                                    {
                                                        <PatitasRegistrarProvincia />
                                                    }
                                                </Field>
                                                {props.errors.provincia && props.touched.provincia && (
                                                    <div className="text-danger ml-3"><small>{props.errors.provincia}</small></div>
                                                )}
                                            </div> 
                    
                                            <div className="form-group col-md-4">
                                                <label>Distrito</label>
                                                <Field
                                                    className="form-control"
                                                    as="select"
                                                    name="distrito"
                                                    onChange={props.handleChange}
                                                >
                                                    <option selected disabled value="">Distrito</option>
                                                    {
                                                        <PatitasRegistrarDistrito provincia = {props.values.provincia} />
                                                    }
                                                </Field>
                                                {props.errors.distrito && props.touched.distrito && (
                                                    <div className="text-danger ml-3"><small>{props.errors.distrito}</small></div>
                                                )}
                                            </div>
                                        </div> 

                                        <div className="form-row">
                                            <div className="form-group col-md-7">
                                                <label>Dirección</label>
                                                <input
                                                    className="form-control"
                                                    placeholder="Dirección o referencia"
                                                    type="text"
                                                    name="direccion"
                                                    value={props.values.direccion}
                                                    onChange={props.handleChange}
                                                />
                                                {props.errors.direccion && props.touched.direccion && (
                                                    <div className="text-danger ml-3"><small>{props.errors.direccion}</small></div>
                                                )}
                                            </div>
                    
                                            <div className="form-group col-md-5">
                                                <label>Fecha</label>
                                                <input
                                                    max={fechaHoy}
                                                    className="form-control"
                                                    placeholder="Fecha"
                                                    type="date"
                                                    name="fecha"
                                                    value={props.values.fecha}
                                                    onChange={props.handleChange}
                                                />
                                                {props.errors.fecha && props.touched.fecha && (
                                                    <div className="text-danger ml-3"><small>{props.errors.fecha}</small></div>
                                                )}
                                            </div>
                                        </div>

                                        <div className="form-row">
                                            <div className="form-group col-md-4">
                                                <label>Telefono</label>
                                                <input
                                                    className="form-control"
                                                    placeholder="Teléfono"
                                                    type="tel"
                                                    name="telefono"
                                                    value={props.values.telefono}
                                                    onChange={props.handleChange}
                                                />
                                                {props.errors.telefono && props.touched.telefono && (
                                                    <div className="text-danger ml-3"><small>{props.errors.telefono}</small></div>
                                                )}
                                            </div>
                    
                                            <div className="form-group col-md-8">
                                                <label>Email</label>
                                                <input
                                                    className="form-control"
                                                    placeholder="Email"
                                                    type="email"
                                                    name="email"
                                                    value={props.values.email}
                                                    onChange={props.handleChange}
                                                />
                                                {props.errors.email && props.touched.email && (
                                                    <div className="text-danger ml-3"><small>{props.errors.email}</small></div>
                                                )}
                                            </div>
                                        </div>

                                        <div className="d-flex justify-content-end mt-2">
                                            <button type="button" className="btn btn-secondary mr-3" data-dismiss="modal">Cerrar</button>
                                            <button type="submit" className="btn btn-primary" data-toggle="modal" data-target="#modalEditar">Guardar cambios</button>
                                        </div>

                                    </form>

                                )}

                            </Formik>
                            
                        </div>
                    </div>
                </div>
            </div>

            <div className="modal fade" id="modalCrear" tabIndex="-1">

                <div className="modal-dialog">

                    <div className="modal-content">

                        <div className="modal-header">
                            <h5 className="modal-title">Crear Registro</h5>
                            <button type="button" className="close collapse__icon" data-dismiss="modal">
                            <span>&times;</span>
                            </button>
                        </div>

                        <div className="modal-body">

                            <Formik
                                initialValues = {{
                                    estado: "",             /* radio             */
                                    especie: "",               /* select            */
                                    descripcion: "",        /* input text        */
                                    imagen: "",               /* input file        */
                                    departamento: "Lima",   /* string            */
                                    provincia: "",          /* select            */
                                    distrito: "",           /* select            */
                                    direccion: "",          /* input text        */
                                    fecha: "",              /* input date        */
                                    telefono: "",           /* input tel         */
                                    email: ""               /* input email       */
                                }}

                                validationSchema = { Yup.object().shape({

                                    estado: Yup.string().required("requerido"),             
                                    especie: Yup.string().required("requerido"),               
                                    descripcion: Yup.string().trim().required("requerido"),        
                                    imagen: Yup.string(),               
                                    departamento: Yup.string(),  
                                    provincia: Yup.string().required("requerido"),         
                                    distrito: Yup.string(),          
                                    direccion: Yup.string().trim().required("requerido"),          
                                    fecha: Yup.string().required("requerido"),              
                                    telefono: Yup.string().matches(/([0-9]{7}|[0-9]{9})/, 'no válido', {excludeEmptyString: true} ),          
                                    email: Yup.string()               

                                })}

                                onSubmit = { (values, actions) => {
                                    postMascota(values).then((data) => {
                                        actions.setSubmitting(false);
                                        actions.resetForm();
                                        setRefresh(refresh+1);
                                    })
                                    Swal.fire({
                                        width: "400px",
                                        title: "¡Hecho!",
                                        text: "Mascota registrada",
                                        icon: "success",
                                        iconColor: "#56cc9d",
                                        timer: 1000,
                                        position: "top-right",
                                        showConfirmButton: false
                                    });
                                }}
                            >
                                {props => (
                                    <form className="px-3 py-2" onSubmit={props.handleSubmit}>

                                        <div className="form-group">
                                            <label>Estado</label>
                                                <div className="d-md-flex">
                                                    <label className="mr-4">
                                                        <Field 
                                                            className="mr-2"
                                                            type="radio" 
                                                            name="estado" 
                                                            value="Perdido"
                                                            onChange={props.handleChange}
                                                        />
                                                        Mascota Perdida
                                                    </label>
                                                    <label>
                                                        <Field 
                                                            className="mr-2"
                                                            type="radio" 
                                                            name="estado" 
                                                            value="Encontrado"
                                                            onChange={props.handleChange}
                                                        />
                                                        Mascota Encontrada
                                                    </label>
                                                </div>
                                                {props.errors.estado && props.touched.estado && (
                                                    <div className="text-danger ml-3"><small>{props.errors.estado}</small></div>
                                                )}
                                        </div>

                                        <div className="form-row">
                                            <div className="form-group col-md-4">
                                                <label>Tipo</label>
                                                <Field
                                                    className="form-control"
                                                    as="select"
                                                    name="especie"
                                                    onChange={props.handleChange}
                                                >
                                                    <option selected disabled value="">Tipo</option>
                                                    <option value="Perro">Perro</option>
                                                    <option value="Gato">Gato</option>
                                                    <option value="Conejo">Conejo</option>
                                                    <option value="Roedor">Roedor</option>
                                                    <option value="Ave">Ave</option>
                                                    <option value="Reptil">Reptil</option>
                                                    <option value="Otro">Otro</option>
                                                </Field>
                                                {props.errors.especie && props.touched.especie && (
                                                    <div className="text-danger ml-3"><small>{props.errors.especie}</small></div>
                                                )}
                                            </div> 
                    
                                            <div className="form-group col-md-8">
                                                <label>Descripción</label>
                                                <Field
                                                    className="form-control"
                                                    placeholder="Añade una descripción"
                                                    rows="3"
                                                    as="textarea"
                                                    name="descripcion"
                                                    value={props.values.descripcion}
                                                    onChange={props.handleChange}
                                                />
                                                {props.errors.descripcion && props.touched.descripcion && (
                                                    <div className="text-danger ml-3"><small>{props.errors.descripcion}</small></div>
                                                )}
                                            </div>
                                        </div>

                                        <div className="form-group">
                                            <div className="input-group mb-3">
                                                <div className="custom-file">
                                                    <label className="custom-file-label">Sube una foto</label>
                                                    <input
                                                        className="custom-file-input"
                                                        type="file"
                                                        name="imagen"
                                                        value={props.values.imagen}
                                                        onChange={props.handleChange}
                                                    />
                                                    {props.errors.imagen && props.touched.imagen && (
                                                        <div className="text-danger ml-3"><small>{props.errors.imagen}</small></div>
                                                    )}
                                                </div>
                                            </div>
                                        </div>

                                        <div className="form-row">
                                            <div className="form-group col-md-4">
                                                <label>Departamento</label>
                                                <input
                                                    className="form-control"
                                                    placeholder="Lima"
                                                    disabled
                                                    type="text"
                                                    name="departamento"
                                                    value={props.values.departamento}
                                                    onChange={props.handleChange}
                                                />
                                                {props.errors.departamento && props.touched.departamento && (
                                                    <div className="text-danger ml-3"><small>{props.errors.departamento}</small></div>
                                                )}
                                            </div>

                                            <div className="form-group col-md-4">
                                                <label>Provincia</label>
                                                <Field
                                                    className="form-control"
                                                    as="select"
                                                    name="provincia"
                                                    onChange={props.handleChange}
                                                >
                                                    <option selected disabled value="">Provincia</option>
                                                    {
                                                        <PatitasRegistrarProvincia />
                                                    }
                                                </Field>
                                                {props.errors.provincia && props.touched.provincia && (
                                                    <div className="text-danger ml-3"><small>{props.errors.provincia}</small></div>
                                                )}
                                            </div> 
                    
                                            <div className="form-group col-md-4">
                                                <label>Distrito</label>
                                                <Field
                                                    className="form-control"
                                                    as="select"
                                                    name="distrito"
                                                    onChange={props.handleChange}
                                                >
                                                    <option selected disabled value="">Distrito</option>
                                                    {
                                                        <PatitasRegistrarDistrito provincia = {props.values.provincia} />
                                                    }
                                                </Field>
                                                {props.errors.distrito && props.touched.distrito && (
                                                    <div className="text-danger ml-3"><small>{props.errors.distrito}</small></div>
                                                )}
                                            </div>
                                        </div> 

                                        <div className="form-row">
                                            <div className="form-group col-md-7">
                                                <label>Dirección</label>
                                                <input
                                                    className="form-control"
                                                    placeholder="Dirección o referencia"
                                                    type="text"
                                                    name="direccion"
                                                    value={props.values.direccion}
                                                    onChange={props.handleChange}
                                                />
                                                {props.errors.direccion && props.touched.direccion && (
                                                    <div className="text-danger ml-3"><small>{props.errors.direccion}</small></div>
                                                )}
                                            </div>
                    
                                            <div className="form-group col-md-5">
                                                <label>Fecha</label>
                                                <input
                                                    max={fechaHoy}
                                                    className="form-control"
                                                    placeholder="Fecha"
                                                    type="date"
                                                    name="fecha"
                                                    value={props.values.fecha}
                                                    onChange={props.handleChange}
                                                />
                                                {props.errors.fecha && props.touched.fecha && (
                                                    <div className="text-danger ml-3"><small>{props.errors.fecha}</small></div>
                                                )}
                                            </div>
                                        </div>

                                        <div className="form-row">
                                            <div className="form-group col-md-4">
                                                <label>Telefono</label>
                                                <input
                                                    className="form-control"
                                                    placeholder="Teléfono"
                                                    type="tel"
                                                    name="telefono"
                                                    value={props.values.telefono}
                                                    onChange={props.handleChange}
                                                />
                                                {props.errors.telefono && props.touched.telefono && (
                                                    <div className="text-danger ml-3"><small>{props.errors.telefono}</small></div>
                                                )}
                                            </div>
                    
                                            <div className="form-group col-md-8">
                                                <label>Email</label>
                                                <input
                                                    className="form-control"
                                                    placeholder="Email"
                                                    type="email"
                                                    name="email"
                                                    value={props.values.email}
                                                    onChange={props.handleChange}
                                                />
                                                {props.errors.email && props.touched.email && (
                                                    <div className="text-danger ml-3"><small>{props.errors.email}</small></div>
                                                )}
                                            </div>
                                        </div>

                                        <div className="d-flex justify-content-end mt-2">
                                            <button type="button" className="btn btn-secondary mr-3" data-dismiss="modal">Cancelar</button>
                                            <button type="submit" className="btn btn-primary" data-toggle="modal" data-target="#modalCrear">Registrar</button>
                                        </div>

                                    </form>
                                )}

                            </Formik>
                            
                        </div>
                    </div>
                </div>
            </div>
            <div className="container">
                <div className="row">
                    <div className="col mt-5 mb-4">
                        <h1 className="text-center">Mascotas Registradas</h1>
                    </div>
                </div>
                <div className="row">
                    <div className="col mb-3 d-flex justify-content-end">
                        <button type="button" className="btn btn-primary rounded-circle" data-toggle="modal" data-target="#modalCrear"><i className="fas fa-plus"></i></button>
                    </div>
                </div>
                <div className="row">
                    <div className="col mb-5">
    
                        <MDBDataTableV5 responsive data={datatable} />
    
                    </div>
                </div>
            </div>
        </main>
    )
}

